html,
body {
  height: 100vh;
  width: 100%;
  margin: 0;
  --scrollbarBG: #cfd8dc;
  --thumbBG: #90a4ae;
  scrollbar-width: thin;
  scrollbar-color: var(--thumbBG) var(--scrollbarBG);
  padding: 0;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}

body {
  background: #070516; /* fallback for old browsers */
  color: white;
  /* font-family: 'Indie Flower', cursive;  Masalda kullan覺lacak */
  font-family: "Pangolin", cursive;
  /* font-family: 'Patrick Hand', cursive;  blogda kullan覺lcak */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  padding: 0;
  margin: 0;
  outline: none;
  box-sizing: border-box;
}

#root {
  width: 100%;
  height: 90vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

::-webkit-scrollbar {
  width: 11px;
}

::-webkit-scrollbar-track {
  background: var(--scrollbarBG);
  border-radius: 3rem;
}

::-webkit-scrollbar-thumb {
  background-color: var(--thumbBG);
  border-radius: 3rem;
  border: 3px solid var(--scrollbarBG);
}

blockquote {
  font-size: 0.9rem;
  border-left: 4px solid rgb(175, 163, 163);
  padding-left: 0.4rem;
  margin-left: 0;
  font-style: italic;
}
.push {
  height: 150px;
}
.MuiGrid-spacing-xs-4 {
  margin: 0 !important;
  width: auto !important;
}

.App {
    /* position: relative; */
}
#logo {
    position: fixed;
    top: 0;
    left: 0;
    margin: 50px;
    z-index: 1000;
    height: 120px;
    width: 120px;
}

@media (max-width: 769px) {
    #logo {
        margin: 20px;
        height: 40px;
        width: 40px;
    }
}
.countup {
    font-size: .9em;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: 600;
    color:wheat;
    margin: 15px auto;
}

.fWohKN {
    bottom:25px !important;
    z-index: 1000;
}
footer {
    border-top: 1px solid rgba(187, 164, 164, 0.5);
    text-align: center;
    width: 100%;
    height: 50px;
    background-color:#e43647;
    position: fixed;
    z-index: 2;
    bottom: 0;
    left: 0;
    right: 0;
}
.scroll-top-div {
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    padding: 0.3rem;
  }
  
  .scroll-top-div svg {
    font-size: 2rem;
  }
  
  .scroll-top-div:hover {
    background-color: rgb(85, 19, 19);
    transition: 300ms;
  }
  
  
  @media (max-width: 500px) {
      .scroll-top-div svg{
          font-size: 1rem;
      }

      .fWohKN {
        bottom:25px !important;
        z-index: 1000;
    }
  }
.container {
    width: 85%;
    margin: auto;
}

#page-title{
    background: linear-gradient(to bottom,  rgba(223, 94, 94, 0.3), transparent);
    width: 100%;
    color: #03e9f4;
    height: 100vh;
    align-items: center;
    justify-content: center;
    display: flex;
    font-size: 3rem;
    font-weight: 700;
    font-family: 'Dancing Script';
    text-transform: uppercase;
}

@media screen and (max-width: 769px) {
    #page-title {
        font-size: 2rem;
    }
}
#messageArea {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  border: 1px solid;
}

.message-users {
  border-bottom: 1px solid;
  font-family: sans-serif;
  width: 100%;
  height: 15%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-evenly;
  background: linear-gradient(to right, #e6cbcb, #b6e98c);
}

.message-users h3 {
  font-size: 24px;
  border-bottom: 1px solid;
}

#users {
  list-style-type: none;
}

#chat {
  width: 100%;
  height: 70%;
  background-image: url(/static/media/slide3.e7b93334.jpeg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  overflow-y: auto;
}

.message-box {
  margin: 0 auto;
  width: 95%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.message-box p {
  line-height: 1.6rem;
  letter-spacing: 1px;
  word-wrap: break-word;
  margin-top: 0.5rem;
  padding: 0.5rem;
  max-width: 80%;
  white-space: pre-wrap;
  border-radius: 2rem;
}

.message-box p:last-of-type {
  margin-bottom: 1rem;
}

#messageForm {
  height: 15%;
  display: flex;
}

#messageForm > div {
  width: 85%;
  height: 100% !important;
}

#message {
  width: 85%;
  resize: none;
  background: linear-gradient(
    to right,
    #010101,
    #0d1b24
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-size: 1.2rem;
  font-family: inherit;
  padding: 0 0.3rem;
  color: #d7d7d7;
}
.send-message {
    padding: 0 !important;
    width: 15%;
}

.send-message svg {
    color: white;
}

@media screen and (max-width: 769px) {
    #messageForm input[type="submit"] {
        width: 30%;
        font-size: .7rem;
    }
}
